import React, {useState} from 'react';
import {BrowserRouter, Route, Routes} from "react-router-dom";
import Main from "./components/page/main";
import {SideMenuContext} from "./context/SideMenuContext";

function App() {
  const [isSideMenuPoped, setSideMenuPop] = useState(false);
  const [isTopMenuPoped, setTopMenuPop] = useState(false);
  const [topMenuContent, setTopMenuContent] = useState<HTMLElement | null>(null);

  const setSideMenuPoped = () => {
    if (isSideMenuPoped) {
      document.body.style.overflowY = 'auto';
      return setSideMenuPop(!isSideMenuPoped);
    }

    document.body.style.overflowY = 'hidden';
    return setSideMenuPop(!isSideMenuPoped);
  }

  const setTopMenuPoped = (state: boolean) => {
    if (!state) {
      document.body.style.overflowY = 'auto';
      return setTopMenuPop(false);
    }

    document.body.style.overflowY = 'hidden';
    return setTopMenuPop(true);
  }

  return (
    <SideMenuContext.Provider value={{
      isSideMenuPoped,
      isTopMenuPoped,
      setSideMenuPoped,
      setTopMenuPoped,
      topMenuContent,
      setTopMenuContent
    }}>
      <BrowserRouter>
        <Routes>
          <Route path="/*" element={<Main/>}/>
        </Routes>
      </BrowserRouter>
    </SideMenuContext.Provider>
  );
}

export default App;
