import {FC} from "react";
import "./Footer.scss";

import {ReactComponent as Logo} from "./../../../icons/logo.svg";

import {ReactComponent as Instagram} from "./../../../icons/socialMedia/inst.svg";
import {ReactComponent as Telegram} from "./../../../icons/socialMedia/telegram.svg";
import {ReactComponent as VK} from "./../../../icons/socialMedia/VK.svg";
import {ReactComponent as Facebook} from "./../../../icons/socialMedia/Facebook.svg";
import {ReactComponent as Odnoklassniki} from "./../../../icons/socialMedia/OK.svg";
import {ReactComponent as Twitter} from "./../../../icons/socialMedia/Twitter.svg";
import {ReactComponent as Youtube} from "./../../../icons/socialMedia/Youtube.svg";
import {ReactComponent as YandexZen} from "./../../../icons/socialMedia/Yandex Zen.svg";
import {ReactComponent as AgeProtected} from "./../../../icons/18+.svg";
import CustomLink from "../customLink";

interface FooterProps {

}

const Footer: FC<FooterProps> = ({}) => {
  return (
    <footer className={'mainFooter'}>
      <div className={'mainFooter__content content'}>
        <section className={'content__logo logo'}>
          <Logo/>
        </section>
        <section className={'content__ads ads'}>
          <span className={'ads__heading'}></span>
          <span className={'ads__item ads__item_highlighted'}>  </span>
          <span className={'ads__item'}> </span>
          <span className={'ads__item'}> ,    </span>
          <span className={'ads__item'}> </span>
        </section>
        <section className={'content__contact contact'}>
          <span className={'contact__heading'}>  </span>
          <span className={'contact__item'}>info@v102.ru</span>
          <span className={'contact__item'}>(8442) 78-19-76</span>
          <span className={'contact__item'}>+7 937 55-66-102</span>
        </section>
        <section className={'content__social social'}>
          <div className={'social__info info'}>
            <span className={'info__heading'}>  </span>
            <div className={'info__links links'}>
              <CustomLink to={''} className={'links__item'}>
                <Instagram/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <Telegram/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <VK/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <Facebook/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <Odnoklassniki/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <Twitter/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <Youtube/>
              </CustomLink>
              <CustomLink to={''} className={'links__item'}>
                <YandexZen/>
              </CustomLink>
            </div>
          </div>
          <div className={'social__copyright copyright'}>
            <div className={'copyright__logo'}>
              <AgeProtected/>
            </div>
            <span className={'copyright__text'}>2006-2021  </span>
          </div>
        </section>
      </div>
    </footer>
  )
}

export default Footer;