import {FC, useState} from "react";
import "./SocialMediaExpandedList.scss";
import {ReactComponent as Instagram} from "./../../../icons/socialMedia/inst.svg";
import {ReactComponent as VK} from "./../../../icons/socialMedia/VK.svg";
import {ReactComponent as Telegram} from "./../../../icons/socialMedia/telegram.svg";
import {ReactComponent as TheRest} from "./../../../icons/socialMedia/allTheRest.svg";
import {ReactComponent as Facebook} from "./../../../icons/socialMedia/Facebook.svg";
import {ReactComponent as Twitter} from "./../../../icons/socialMedia/Twitter.svg";
import {ReactComponent as Odnoklassniki} from "./../../../icons/socialMedia/OK.svg";
import {ReactComponent as Youtube} from "./../../../icons/socialMedia/Youtube.svg";
import {ReactComponent as YandexZen} from "./../../../icons/socialMedia/Yandex Zen.svg";

interface SocialMediaExpandedListProps {
  expanded?: boolean;
}

const SocialMediaExpandedList: FC<SocialMediaExpandedListProps> = ({expanded = false}) => {
  const [isExpanded, setExpanded] = useState(false);

  function onExpand(state: boolean) {
    setExpanded(state);
  }

  if (!expanded) {
    return (
      <div className={`socialMediaExpandedList
    ${isExpanded ? 'socialMediaExpandedList_expanded' : 'socialMediaExpandedList_collapsed'}`}>
        <>
          <div className={'socialMediaExpandedList__item'}>
            <Instagram/>
          </div>
          <div className={'socialMediaExpandedList__item'}>
            <Telegram/>
          </div>
          <div className={'socialMediaExpandedList__item'}>
            <VK/>
          </div>
          {isExpanded ?
            <>
              <div className={'socialMediaExpandedList__item socialMediaExpandedList__item_expanding'}>
                <Facebook/>
              </div>
              <div className={'socialMediaExpandedList__item socialMediaExpandedList__item_expanding'}>
                <Odnoklassniki/>
              </div>
              <div className={'socialMediaExpandedList__item socialMediaExpandedList__item_expanding'}>
                <Twitter/>
              </div>
              <div className={'socialMediaExpandedList__item socialMediaExpandedList__item_expanding'}>
                <Youtube/>
              </div>
              <div className={'socialMediaExpandedList__item socialMediaExpandedList__item_expanding'}>
                <YandexZen/>
              </div>
            </> : null}
          <div
            onClick={() => onExpand(!isExpanded)}
            className={`socialMediaExpandedList__item socialMediaExpandedList__rest ${isExpanded && 'socialMediaExpandedList__rest_expanded'}`}>
            <TheRest/>
          </div>
        </>
      </div>
    )
  }

  return (
    <div className={`socialMediaExpandedList socialMediaExpandedList_mobile_expanded`}>
        <div className={'socialMediaExpandedList__item'}>
          <Instagram/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <Telegram/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <VK/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <Facebook/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <Odnoklassniki/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <Twitter/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <Youtube/>
        </div>
        <div className={'socialMediaExpandedList__item'}>
          <YandexZen/>
        </div>
    </div>
  )
}

export default SocialMediaExpandedList;