import {FC} from "react";
import "./SectionPage.scss";
import Advertisement from "../../advertisement";
import FeedCard from "../../feedCard";
import Recommended from "../../recommended";
import ShowMoreButton from "../../showMoreButton";
import {useMediaQuery} from "react-responsive";

interface SectionPageProps {
  heading: string;
}

const SectionPage: FC<SectionPageProps> = ({heading}) => {
  const isNotDesktop = useMediaQuery({minWidth: '320px', maxWidth: '1439px'});

  return (
    <section className={'sectionPage'}>
      <h1 className={'sectionPage__heading'}>{heading}</h1>
      <section className={'sectionPage__mainContent mainContent'}>
        {isNotDesktop ?
          <section className={'mainContent__mainCards'}>
            <Advertisement/>
            <Advertisement/>
            <Advertisement/>
            <FeedCard type={'full'}
                      heading={'       '}/>
            <FeedCard type={'half'}
                      heading={'          4  '}/>
            <FeedCard type={'half'}
                      heading={'          4  '}/>
            <FeedCard type={'half'}
                      heading={'          4  '}/>
          </section>
          : <>
            <section className={'mainContent__asideCardsList asideCardsList'}>
              <FeedCard type={'full'}
                        heading={'       '}/>
              <FeedCard type={'half'}
                        heading={'          4  '}/>
              <FeedCard type={'half'}
                        heading={'          4  '}/>
              <FeedCard type={'half'}
                        heading={'          4  '}/>
            </section>
            <aside className={'mainContent__asideAds'}>
              <Advertisement/>
              <Advertisement/>
              <Advertisement/>
            </aside>
          </>}
      </section>
      <section className={'sectionPage__additionalContent additionalContent'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
      </section>
      <section className={'sectionPage__recommendedContent recommendedContent'}>
        <Recommended heading={''}/>
      </section>
      <section className={'sectionPage__subContent subContent'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <ShowMoreButton/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
      </section>
    </section>
  )
}

export default SectionPage;