import {FC} from "react";
import "./RenderComments.scss";
import {IComment} from "../../../interface";
import SeparateComment from "../separateComment";

interface RenderCommentsProps {
  comments: IComment[];
  depth?: number;
}

// @ts-ignore
const RenderComments: FC<RenderCommentsProps> = ({comments, depth = 1}) => {
  return (
    comments.map((comment) => {
      if (Array.isArray(comment.comments) && comment.comments.length) {
        return (
          <>
            <SeparateComment
              author={comment.author}
              date={comment.date}
              text={comment.text}
              depthLevel={depth}/>
            <RenderComments depth={depth + 1} comments={comment.comments}/>
          </>
        )
      }

      return <SeparateComment
        author={comment.author}
        date={comment.date}
        text={comment.text}
        depthLevel={depth}/>
    })
  )
}

export default RenderComments;