import {FC, useMemo, useRef} from "react";
import "./MediaPreview.scss";
import FeedCard from "../feedCard";
import Advertisement from "../advertisement";

import {ReactComponent as Go} from "../../../icons/goOrange.svg";
import MenuUnderlineSwitcher from "../menuUnderlineSwitcher";
import {useMediaQuery} from "react-responsive";

interface MediaPreviewProps {

}

const MediaPreview: FC<MediaPreviewProps> = ({}) => {
  const isMobile = useMediaQuery({minWidth: '320px', maxWidth: '767px'});
  const isTablet = useMediaQuery({minWidth: '768px', maxWidth: '1439px'});

  const cards = useMemo(() => {
    if (isMobile) {
      return (
        <FeedCard type={'photo'}
                  heading={'       '}/>
      )
    } else if (isTablet) {
      return (
        <>
          <FeedCard type={'photo'}
                    heading={'       '}/>
          <FeedCard type={'photo'}
                    heading={'     '}/>
        </>
      )
    }

    return (
      <>
        <FeedCard type={'photo'}
                  size={'big'}
                  heading={'        -  '}/>
        <FeedCard type={'photo'}
                  heading={'       '}/>
        <FeedCard type={'photo'}
                  heading={'     '}/>
      </>
    )
  }, [isMobile, isTablet]);

  return (
    <section className={'mediaPreview'}>
      <div className={'mediaPreview__info info'}>
        <h2 className={'info__heading'}></h2>
        <div className={'info__content content'}>
          <div className={'content__mediaList mediaList'}>
            <div className={'mediaList__navigation navigation'}>
              <MenuUnderlineSwitcher
                fontColor={'#FFFFFF'}
                floatLastRight={true}
                headers={[
                  <span className={'navigation__item'}></span>,
                  <span className={'navigation__item'}></span>,
                  <span className={'navigation__item navigation__item_type_withSvg'}>
                  
                <Go/>
              </span>
                ]}/>
            </div>
            <Advertisement/>
            { cards }
          </div>
        </div>
      </div>
    </section>
  )
}

export default MediaPreview;