import {FC} from "react";
import "./Article.scss";
import {useLocation} from "react-router-dom";
import {ArticleType} from "../../../interface";
import BigArticle from "../../../UI/pageTemplates/bigArticle";
import SmallArticle from "../../../UI/pageTemplates/smallArticle";

interface ArticleProps {

}

const Article: FC<ArticleProps> = ({}) => {
  const location = useLocation();
  // @ts-ignore
  const { articleType }: { articleType: ArticleType } = location.state;

  switch (articleType) {
    case 'bigArticle':
      return <BigArticle heading={'         '}/>
    case 'smallArticle':
      return <SmallArticle heading={'         '}/>
  }
}

export default Article;