import {FC} from "react";
import "./AllTags.scss";
import Tag from "../tag";

interface AllTagsProps {

}

const AllTags: FC<AllTagsProps> = ({}) => {
  const defaultTags = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '  ',
    '',
    ' '
  ];

  return (
    <section className={'allTags'}>
      {defaultTags.map((tag) => {
        return <Tag name={tag}/>
      })}
    </section>
  )
}

export default AllTags;