import {FC, ReactNode, MouseEvent, useRef, useState, useEffect} from "react";
import "./MenuUnderlineSwitcher.scss";

interface MenuUnderlineSwitcherProps {
  headers: string[] | ReactNode[];
  floatLastRight?: boolean;
  lineColor?: string;
  underlineColor?: string;
  fontColor?: string;
  wrapperItemClassName?: string;
  wrapperClassName?: string;
}

const MenuUnderlineSwitcher: FC<MenuUnderlineSwitcherProps> = ({
  headers,
  lineColor = '#FFFFFF',
  underlineColor =  '#FA6E03',
  fontColor = '#282C3B',
  floatLastRight = false,
  wrapperItemClassName = '',
  wrapperClassName = ''
}) => {
  const [selected, setSelected] = useState(0);
  const itemsRef = useRef<Array<HTMLDivElement>>([]);
  const underlineRef = useRef<HTMLHRElement>(null);

  useEffect(() => {
    underlineRef.current!.width = itemsRef.current[0]!.offsetWidth + 'px';
  }, [itemsRef, underlineRef]);

  const onMediaSwitch = (e: MouseEvent) => {
    const nodeElement = e.currentTarget as HTMLElement;
    const index = Number(nodeElement.dataset.id);

    setSelected(index);

    const left = nodeElement.offsetLeft;
    const lineWidth = nodeElement.offsetWidth;

    underlineRef.current!.width = lineWidth + 'px';
    underlineRef.current!.style.marginLeft = left + 'px';
  }

  return (
    <div style={{borderBottom: `2px solid ${lineColor}`}} className={`menuUnderlineSwitcher ${wrapperClassName}`}>
      {headers.map((header, index, array) => {
        const floatRight = floatLastRight && index === array.length - 1
        && {
            marginLeft: 'auto',
            marginRight: 0
          } || {}

        return (
          <div  className={`menuUnderlineSwitcher__item ${wrapperItemClassName} ${index === selected && 'menuUnderlineSwitcher__item_active'}`}
                onClick={onMediaSwitch}
                ref={el => itemsRef.current[index] = el!}
                style={{
                  color: fontColor,
                  ...floatRight
                }}
                data-id={index}
                >
            {header}
          </div>
        )
      })}
      <hr style={{
        backgroundColor: underlineColor
      }}
          className={'menuUnderlineSwitcher__line'}
          ref={underlineRef}/>
    </div>
  )
}

export default MenuUnderlineSwitcher;