import {FC, useContext, useEffect, useRef, useState} from "react";
import {ReactComponent as Logo} from "../../../icons/logo.svg";
import "./Header.scss";
import CustomLink from "../customLink";
import Weather from "../weather";
import SocialMediaExpandedList from "../socialMediaExpandedList";
import {ScrollingCarousel} from "@trendyol-js/react-carousel";

import {ReactComponent as Edition} from "./../../../icons/edition.svg";
import {ReactComponent as Notification} from "./../../../icons/notification.svg";
import {ReactComponent as Search} from "./../../../icons/search.svg";
import {ReactComponent as RightArrow} from "./../../../icons/rightArrow.svg";
import {ReactComponent as Ruport} from "./../../../icons/ruport.svg";
import {ReactComponent as Burger} from "./../../../icons/burger.svg";
import {ReactComponent as Cancel} from "./../../../icons/cancel.svg";
import SectionLink from "../sectionLink";
import {ILink} from "../../interface";
import {headerLinks, sectionLinks} from "../../constants";
import {useLocation} from "react-router-dom";

interface HeaderProps {

}

const Header: FC<HeaderProps> = ({}) => {
  const mobileHeaderRef = useRef<HTMLElement | null>(null);
  const location = useLocation();
  const [isMobileOpen, setMobileOpen] = useState(false);

  useEffect(() => {
    setMobileOpen(false);
  }, [location])

  return (
    <header className={'mainHeader'}>
      <section className={`mainHeader_fixed`}>
        <section ref={mobileHeaderRef}
                 className={`mainHeader__wrapper wrapper`}>
          <nav className={'wrapper__navigation navigation'}>
            <CustomLink to={'/'} className={'navigation__logo'}>
              <Logo/>
            </CustomLink>
            <div className={'navigation__links links'}>
              {headerLinks.map((link: ILink) =>
                <CustomLink
                  className={'links__item'}
                  activeClassName={'links__item_active'}
                  to={link.href}>
                  {link.name}
                </CustomLink>
              )}
            </div>
            <div className={'navigation__rest rest'}>
              <div className={'rest__weather weather'}>
                <Weather type={'cloudy'} temp={21}/>
              </div>
              <div className={'rest__socialMedia'}>
                <SocialMediaExpandedList/>
              </div>
              <div className={'rest__buttons buttons'}>
                <CustomLink className={'buttons__link'}
                            to={''}>
                  <Edition/>
                </CustomLink>
                <CustomLink className={'buttons__link'}
                            to={''}>
                  <Notification/>
                </CustomLink>
                <CustomLink className={'buttons__link'}
                            to={''}>
                  <Search/>
                </CustomLink>
                <div onClick={() => setMobileOpen(!isMobileOpen)} className={'buttons__mobileMenu'}>
                  {
                    isMobileOpen ?
                      <Cancel/>
                      : <Burger/>
                  }
                </div>
              </div>
            </div>
          </nav>
        </section>
        <section className={`mainHeader__mobileLinks mobileLinks ${isMobileOpen ? ' mobileLinks_opened' : ''}`}>
          <section className={'mobileLinks__social'}>
            { isMobileOpen ? <SocialMediaExpandedList expanded/> : null}
          </section>
          <section className={'mobileLinks__links links'}>
            {headerLinks.map((link: ILink) =>
              <CustomLink
                className={'links__item'}
                activeClassName={'links__item_active'}
                to={link.href}>
                {link.name}
              </CustomLink>
            )}
          </section>
          <section className={'mobileLinks__rest rest'}>
            <CustomLink className={'rest__link'}
                        to={''}>
              <Edition/>
            </CustomLink>
            <CustomLink className={'rest__link'}
                        to={''}>
              <Notification/>
            </CustomLink>
          </section>
          <section className={'mobileLinks__sections sections'}>
            {sectionLinks.map((link: ILink) => (
              <CustomLink className={'sections__link'} to={link.href}>
                {link.name}
              </CustomLink>
            ))}
          </section>
        </section>
        {isMobileOpen ?
          <div onClick={() => setMobileOpen(false)} className={'mobileLinks__outside'}/> : null}
        <section className={'mainHeader__sections sections'}>
          <div className={'sections__list list'}>
            <ScrollingCarousel leftIcon={
              <div className={'list__arrow list__leftIcon'}>
                <RightArrow transform={"scale(-1, 1)"}/>
              </div>
            } rightIcon={
              <div className={'list__arrow list__rightIcon'}>
                <RightArrow/>
              </div>}>
              {sectionLinks.map((link: ILink) => <SectionLink name={link.name} to={link.href}/>)}
            </ScrollingCarousel>
          </div>
        </section>
      </section>
      <section className={'mainHeader__notification notification'}>
        <div className={'notification__content content'}>
          <div className={'content__logo'}>
            <Ruport/>
          </div>
          <span className={'content__text'}>
                       
          </span>
        </div>
      </section>
    </header>
  )
}

export default Header;