import {FC, useCallback} from "react";
import "./Weather.scss";

import { ReactComponent as Cloudy} from "./../../../icons/weather/cloudy.svg";

interface WeatherProps {
  type: 'cloudy';
  temp: number;
}

const Weather: FC<WeatherProps> = ({temp, type}) => {
  const WeatherLogo = useCallback(() => {
    switch (type) {
      case "cloudy":
        return <Cloudy/>
    }
  }, [type]);

  return (
    <div className={'weather'}>
      <div className={'weather__logo'}>
        <WeatherLogo/>
      </div>
      <span className={'weather__temp'}>
        {temp}
      </span>
    </div>
  )
}

export default Weather;