import {FC} from "react";
import "./InitialPage.scss";
import Advertisement from "../../../UI/advertisement";
import FeedCard from "../../../UI/feedCard";
import Newsline from "../../../UI/newsline";
import Popular from "../../../UI/popular";
import FeedPreview from "../../../UI/feedPreview";
import MediaPreview from "../../../UI/mediaPreview";
import SpecProjectsPreview from "../../../UI/specProjectsPreview";
import Recommended from "../../../UI/recommended";
import CustomLink from "../../../UI/customLink";

interface InitialPageProps {

}

const InitialPage: FC<InitialPageProps> = ({}) => {
  return (
    <section className={'initialPage'}>
      <section className={'initialPage_centered initialPage__content content'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <FeedCard type={'full'} heading={'-        Nemoloko'}/>
        <Newsline heading={' '} isTopHighlighted={true}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <FeedCard type={'half'} heading={'-        Nemoloko'}/>
        <Popular/>
      </section>
      <section className={'initialPage__preview preview'}>
        <SpecProjectsPreview/>
      </section>
      <section className={'initialPage_centered initialPage__additionalContent additionalContent'}>
        <aside className={'additionalContent__aside aside'}>
          <Newsline heading={''}/>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
        </aside>
        <section className={'additionalContent__newsFeed newsFeed'}>
          <Advertisement/>
          <Advertisement/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'half'}
                    heading={'         '}
                    size={'medium'}/>
          <FeedCard type={'full'}
                    heading={'         '}/>
        </section>
      </section>
      <CustomLink to={'/news/123123'} className={'initialPage__feedPreview'}>
        <FeedPreview/>
      </CustomLink>
      <section className={'initialPage_centered initialPage__recommendedFeed recommendedFeed'}>
        <Recommended heading={''}/>
      </section>
      <section className={'initialPage_centered initialPage__subContent subContent'}>
        <section className={'subContent__cardsList cardsList'}>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
          <FeedCard
            type={'half'}
            size={'big'}
            heading={'           '}/>
          <FeedCard
            type={'half'}
            size={'big'}
            heading={'           '}/>
          <FeedCard
            type={'half'}
            size={'big'}
            heading={'           '}/>
          <FeedCard
            type={'half'}
            size={'big'}
            heading={'           '}/>
          <FeedCard
            type={'full'}
            heading={'        '}/>
        </section>
      </section>
      <section className={'initialPage__media'}>
        <MediaPreview/>
      </section>
      <section className={'initialPage_centered initialPage__ads'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
      </section>
    </section>
  )
}

export default InitialPage;