import {FC} from "react";
import "./Share.scss";
import {shareLinks} from "../../constants";
import ShareCard from "./shareCard";

interface ShareProps {
  isLastAside?: boolean;
  isBorderTop?: boolean;
}

const Share: FC<ShareProps> = ({isLastAside = true, isBorderTop = true}) => {
  return (
    <div className={`share ${isBorderTop ? 'share_border_top' : ''}`}>
      <span className={'share__text'}>:</span>
      <div className={`share__cards ${isLastAside ? 'share__cards_aside' : ''}`}>
        {shareLinks.map((link) => <ShareCard type={link}/>)}
      </div>
    </div>
  )
}

export default Share;